.topnav {
    z-index: 1;
    display: flex;
    justify-content: space-between;
    position: relative;
    position: fixed;
    margin: 0 auto;
    padding: auto 10%;
    width: 100vw;
    height: 6vh;
    background-image: linear-gradient(#070809, #2D3237);
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.logoContainer {
    margin-left: 2.5vw;
    /* width: 60em; */
    height: 100%;
    display: inline-flex;
    justify-content: space-between;
}

.logo {
    margin: auto;
    width: 2.5em;
    height: 2.5em;
    background: url(../../imgs/treeLogo.png);
    background-size: contain;
}

h1 {
    padding-left: 15px;
    text-align: center;
    font-family: 'IM Fell English SC', serif;
    color: white;
    margin: auto;
}

.search {
    margin: auto 2.5vw auto auto;
    padding: auto;
    width: 15vw;
    height: auto;
}

input {
    width: 100%;
    height: 100%;
    border: 5px solid black;
    border-radius: 5px;
    /* text-align: center; */
    font-size: 1.5rem;
    font-family: 'IM Fell English SC', serif;
}

.searchButton {
    background-color: green;
    width: 1rem;
    height: 1rem;
}

